/*
  #overview
  
  */
.u-media {
  // SP 
  &__ltSP {
    display: none;

    @include breakpoint($max-media: sp) {
      display: block; } }

  // Phablet 
  &__ltPH {
    display: none;

    @include breakpoint($max-media: phablet) {
      display: block; } }

  // Tablet 
  &__ltTB {
    display: none;

    @include breakpoint($max-media: tablet) {
      display: block; } }

  // PC 
  &__ltPC {
    display: none;

    @include breakpoint($max-media: pc) {
      display: block; } }

  // SP 
  &__gtSP {
    display: none;

    @include breakpoint($max: false, $min: true, $min-media: sp) {
      display: block; } }

  // Phablet 
  &__gtPH {
    display: none;

    @include breakpoint($max: false, $min: true, $min-media: phablet) {
      display: block; } }

  // Tablet 
  &__gtTB {
    display: none;

    @include breakpoint($max: false, $min: true, $min-media: tablet) {
      display: block; } }

  // PC 
  &__gtPC {
    display: none;

    @include breakpoint($max: false, $min: true, $min-media: pc) {
      display: block; } } }
