&__ordered {
  $root: &;
  margin-top: 14px;
  counter-reset: note-order;

  &:first-child {
    margin-top: 0; }

  $note-size: 2.4em;
  & &--item {
    position: relative;
    margin-top: 8px;
    padding-left: $note-size;

    &:first-child {
      margin-top: 0; }

    &::before {
      content: '' counter(note-order);
      width: $note-size;
      display: inline-block;
      text-align: left;
      margin-left: -#{$note-size};
      counter-increment: note-order; } } }
