/*
  #overview
  

  
   Element  Block 
   Block 
  */
.u-text {
  /*
    #styleguide
    
    ```
    <p class="p u-text__left"></p>
    ```
    */
  &__left {
    text-align: left; }

  /*
    #styleguide
    
    ```
    <p class="p u-text__right"></p>
    ```
    */
  &__right {
    text-align: right; }

  /*
    #styleguide
    
    ```
    <p class="p u-text__center"></p>
    ```
    */
  &__center {
    text-align: center; }

  /*
    #styleguide
    

    ```
    <sup class="sup u-text__marker"></sup>

    <p class="p"><sup class="sup u-text__marker"></sup></p>
    ```
    */
  &__marker {
    font-size: $font-x-small;
    margin: 0 0.2em; }

  /*
    #styleguide
    

    ```
    <dt class="dt u-text__required"></dt>
    ```
    */
  &__required {
    &::after {
      content: '*';
      color: $font-navy;
      margin-left: 0.2em; } }

  /*
    #styleguide
    font-size: 1.2rem

    ```
    <dl class="dl">
      <dt class="dt u-text__small">Lorem ipsum</dt>
      <dd class="dd">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</dd>
    </dl>
    ```
    */
  &__small {
    font-size: 86%; }

  /*
    #styleguide
    font-weight: bold

     em 

    ```
    <p class="p u-text__bold">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
    ```
    */
  &__bold {
    font-weight: bold; }

  /*
    #styleguide
    font-weight: normal

    ```
    <em class="em u-text__weak">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</em>
    ```
    */
  &__weak {
    font-weight: normal; }

  /*
    #styleguide
     data 

    ```
    <p class="p u-text__before" data-before="">Lorem ipsum dolor sit amet, soleat scripta scriptorem cu his.</p>
    ```
    */
  &__before {
    &::before {
      content: attr(data-before); } }

  /*
    #styleguide
     data 

    ```
    <p class="p u-text__after" data-after="">Lorem ipsum dolor sit amet, soleat scripta scriptorem cu his.</p>
    ```
    */
  &__after {
    &::after {
      content: attr(data-after); } }

  /*
    #styleguide
    

     

    ```
    <p class="p"><span class="span u-text__block"></span><span class="span u-text__block"></span></p>
    ```
    */
  &__block {
    display: inline-block; }

  /*
    #styleguide
    

    ```
    <p class="p u-text__canceled">0000004201</p>
    ```
    */
  &__canceled {
    color: $font-gray;
    text-decoration: line-through; }

  &__error {
    color: $font-error;
    font-weight: bold; }

  &__leftGap {
    margin-left: 1em; }

  &__rightGap {
    margin-right: 1em; }

  /*
    #styleguide
    

    ```
    <p class="p u-text__nowrap"></p>
    ```
    */
  &__nowrap {
    white-space: nowrap; }

  /**
   * 
   *
   * ```
   * <p class="p u-text__break">http://longlonglonglonglonglongword.com/</p>
   * ```
   */
  &__break {
    word-break: break-all; }

  /*
    #styleguide
    

    ```
    <p class="p u-text__preWrap">
    </p>
    ```
    */
  &__preWrap {
    white-space: pre-wrap; } }
